<template>
  <div class="home">
    <h1>课后作业单</h1>
    <el-form ref="form" :model="song" label-width="80px">
      <el-form-item label="手指练习">
        <el-input v-model="song.finger"></el-input>
      </el-form-item>
      <el-form-item label="乐曲名称">
        <el-row class="demo-autocomplete">
          <el-col :span="12">
            <el-autocomplete
              class="inline-input"
              v-model="song.name"
              :fetch-suggestions="querySearch"
              @select="handleSelect"
            ></el-autocomplete>
          </el-col>
        </el-row>
        
        <!-- <el-input v-model="song.name"></el-input> -->
      </el-form-item>
      <el-form-item label="开始小节">
        <el-input v-model="song.startnum"></el-input>
      </el-form-item>
      <el-form-item label="结束小节">
        <el-input v-model="song.endnum"></el-input>
      </el-form-item>
       <el-form-item label="每天练习">
        <el-input v-model="song.daynum"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">发送</el-button>
      </el-form-item>

      
  
    </el-form>
    <span style="color:red">{{err}}</span>


    <div v-if="!message==''">
      <h3>课后作业单</h3>
      <h3>基本功天天练: {{song.finger}}</h3>
      <h3>乐曲: {{song.name}}</h3>
      <ul v-for="i in 6">
       <li v-if="song.daynum*i<=song.endnum">第{{i}}天：{{song.daynum*i-song.daynum+1}}到{{song.daynum*i}}小节

       </li>
       <li v-if="song.daynum*i>song.endnum">第{{i}}天：复习</li>

    </ul>
    </div>









  </div>
</template>
<script>
  export default {
    name: "home",
    data() {
      return {
        song: {
          finger:'',
          name: '',
          startnum: '',
          endnum: '',
          daynum: '',
          
        },
        message:"",
        err: '',
        namelist:[
          { "value": "《A大调奏明曲》"},
          { "value": "《G大调布列舞曲》"},
          { "value": "《D大调日耳曼舞曲》"},
          { "value": "《匈牙利歌曲》"},
          { "value": "《小步舞曲》"},
          { "value": "《进行曲》"},
          { "value": "《雪橇派对》"},
          { "value": "《小步舞曲》"},
          { "value": "《两首钢琴小品》"},
          { "value": "《小前奏曲》"},
          { "value": "《F大调小步舞曲》"},
          { "value": "《阿坝夜会》"},
        ],
        state1:'',

      }
    },
    methods: {
       querySearch(queryString, cb) {
        var namelist = this.namelist;
        var results = queryString ? namelist.filter(this.createFilter(queryString)) : namelist;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (namelist) => {
          return (namelist.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect(item){
        console.log(item)
      },
      onSubmit() {
        self = this;
        let song = self.song;
        let msg = "";
        if(song.name==""||song.finger==""||song.daynum==""||song.startnum==""||song.endnum==""){
          self.err = '请输入完整的信息'
        }
        else{

          msg += "课后作业单:"+" \n"+" \n";
          msg += "基本功天天练:"+ song.finger + " \n";
          msg += "乐曲名称:"+ song.name + " \n";
          for(var i=1; i < 7; i++){
            if(song.daynum*i<=song.endnum){
              msg += "第"+i+"天："+(song.daynum*i-song.daynum+1) +"到"+ song.daynum*i +"小节"+" \n";

            }
            if(song.daynum*i>song.endnum){
              msg += "第"+i+"天：复习"+" \n"
            }
          }
          self.message = msg;
          self.sendMsg();
        }
        // console.log(msg)
      },
      sendMsg(msg){
        self = this;
        this.$http.post('http://47.100.211.7:8080/api/',{
          message:self.message,
        }).then((data)=>{
          console.log(data);
        })
      }

  }
}
</script>
<style>
  div{
    width: 15em;
  }
</style>


